import React from 'react';
import ReactEcharts from "echarts-for-react";
import echarts from "echarts";

export default class RateCharts extends React.Component {
	constructor(props) {
		super(props);
    }

    componentWillMount() {

    }

	render() {
        const {title, rateNum} = this.props;
        var option = option = {
            series: [{
                    name: '2019-02-12 ',
                    type: 'gauge',
                    radius: '80%',
                    axisLine: { // 
                        lineStyle: { // lineStyle
                            width: 15,
                            color: [
                                [0, '#539df6'],
                                [0.75, '#539df6'],
                                [1, '#539df6']
                            ]
                        }
                    },
                    splitLine: {
                        show: false,
                        length: 15,
                        lineStyle: {
                            //color:'black'
                        },
                    },
                    "axisTick": {
                        show: false,

                    },
                    axisLabel: {
                        formatter: function(e) {
                            switch (e + "") {
                                case "0":
                                    return "";
                                case "10":
                                    return "";
                                case "20":
                                    return "";
                                case "30":
                                    return "";

                                case "40":
                                    return "";
                                case "50":
                                    return "";

                                case "60":
                                    return "";
                                case "70":
                                    return "";
                                case "80":
                                    return "";
                                case "90":
                                    return "";
                                case "100":
                                    return "";
                                default:
                                    return e;
                            }
                        },
                        textStyle: {
                            fontSize: 14,
                            fontWeight: "",
                            color: '#000'
                        }
                    },
                    detail: {
                        formatter: '{value}%',
                        "textStyle": {
                            "fontSize": 20,
                            fontWeight: '700',
                            "color": '#000',
                        }
                    },
                    title: {
                       "fontSize": "12px",
                        color: '#999',
                        offsetCenter: [0, '50%']
                    },
                    pointer: {
                        show: true,
                        length: '60%',
                    },
                    itemStyle: { // 
                        color: '#539df6'
                    },
                    data: [{
                        value: rateNum,
                        name: title
                    }]
                },
                { //
                    "type": 'pie',
                    "hoverAnimation": false,
                    "legendHoverLink": false,
                    "radius": ['6%', '8%'],
                    "z": 10,
                    "label": {
                        "normal": {
                            "show": false
                        }
                    },
                    "labelLine": {
                        "normal": {
                            "show": false
                        }
                    },
                    "data": [{
                        "value": 80,
                        itemStyle: {
                            normal: {
                                color: "#539df6"
                            }
                        }
                    }]
                },
                { //
                    "type": 'pie',
                    "hoverAnimation": false,
                    "legendHoverLink": false,
                    "radius": ['0%', '6%'],
                    "z": 10,
                    "label": {
                        "normal": {
                            "show": false
                        }
                    },
                    "labelLine": {
                        "normal": {
                            "show": false
                        }
                    },
                    "data": [{
                        "value": 80,
                        itemStyle: {
                            normal: {
                                color: "#ffffff"
                            }
                        }
                    }]
                },
                {
                    name: '', //
                    type: 'gauge',
                    z: 2,
                    radius: '68%',
                    splitNumber: 4,
                    center: ["50%", "0%"], //
                    axisLine: { // 
                        lineStyle: { // lineStyle

                            width: 20,
                            opacity: 0, //
                        }
                    },
                    data: [{
                        show: false,
                        value: '100'
                    }],
                    axisLabel: {
                        show: false
                    },
                    "axisTick": {
                        show: false,

                    },
                    pointer: {
                        show: false
                    },
                    detail: {
                        show: 0
                    },
                    splitLine: {
                        length: 10, //
                        lineStyle: {
                            width: 3,
                            color: '#539df6'

                        } //
                    },
                },
            ]
        };
        const option2 = {  // echarts
            tooltip : {
                formatter: "{a} <br/>{b} : {c}%"
            },
            grid: {
                left: '5px',
                right: '5px',
                containLabel: true
            },
            toolbox: {
                show : false,
                feature: {
                    restore: {},
                    saveAsImage: {}
                }
            },
            series: [
                {
                    name: '',
                    type: 'gauge',
                    detail: {formatter: '{value}%',textStyle:{fontSize:12},
                    },
                    radius: '100%',
                    data: [{value: rateNum, name: title}],
                    title: {
                        fontSize: "12px",
                        color: '#999',
                        offsetCenter: [0, '80%']
                    },
                    min:0,
                    max:100,    //
                    splitNumber:4,  //
                    axisLine: { // 
                        lineStyle: { // lineStyle
                            width: 10,
                            color: [
                                [rateNum/100, "#5DD1FA"],   // 
                                [1, "#539df6"]
                            ]
                        },
                    },
                    // 
                    splitLine: {
                        show: true,
                        length: 15,
                        lineStyle: {
                            color: "auto"
                        }
                    },
                    // 
                    axisTick: {
                        show: true,
                        length: 13,
                        splitNumber: 10,
                        lineStyle: {
                            color: "auto"
                        }
                    },
                    axisLabel: {
                        formatter:function(v){
                            switch (v + '') {
                                case '0' : return '';
                                case '100' : return '';
                                default:
                                    return v;
                            }
                        }
                    },
                    pointer: {
                        width: 7,//
                        length: "50%", //
                        shadowColor: '#ccc', //
                        shadowBlur: 5
                    },
                  }
                ]
            }
		return (
            <ReactEcharts
                style={{width: '100%', height:'100%',padding:'10px 10px'}}
                option={option2}
                notMerge={true}
                lazyUpdate={true}
            />
		)
	}
}



